.contact-us {
  &__info {
    margin: 0 0 24px;
  }

  &--last {
    margin-bottom: 32px;
  }

  &__info-title {
    margin: 0 0 8px;
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__info-link {
    text-decoration: none;
    color: inherit;

    &:hover {
      border-bottom: 1px solid #000;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
